import Vue from 'vue'
import VueRouter from "vue-router";
import all from '../pages/all'
import done from '../pages/done'
import needtodo from '../pages/needtodo'

const router = new VueRouter({
    routes: [
        {
            path: '/All',
            component: all
        },
        {
            path: '/Done',
            component: done
        },
        {
            path: '/Needtodo',
            component: needtodo
        }
    ]
})

export default router