import Vuex from 'vuex'
import vue from 'vue'
vue.use(Vuex)

const actions = {
    addtodo(context, value) {
        context.commit('ADDTODO', value)
    },
    changedone(context, value) {
        context.commit('CHANGEDONE', value)
    },
    deletetodo(context, value) {
        context.commit('DELETETODO', value)
    }
}
const mutations = {
    ADDTODO(state, value) {
        state.todos.unshift(value)
        window.localStorage.setItem('todos', JSON.stringify(state.todos))

    },
    CHANGEDONE(state, value) {
        state.todos.forEach((todo) => {
            if (todo.id == value) {
                todo.isdone = !todo.isdone
            }
        })
        window.localStorage.setItem('todos', JSON.stringify(state.todos))

    },
    DELETETODO(state, value) {
        state.todos = state.todos.filter((todo) => {
            return todo.id != value
        })
        window.localStorage.setItem('todos', JSON.stringify(state.todos))

    }
}
const state = {
    todos: JSON.parse(localStorage.getItem('todos')) || []
}
export default new Vuex.Store({
    actions,
    mutations,
    state,
})