import Vue from "vue";
import App from "./App.vue";
// 引入插件
//import plugins from "./plugins";

// 引入vue-router
import VueRouter from "vue-router";
// 引入路由文件
import router from './router/index'
// 引入vuex中的store
import store from './store/index'
Vue.config.productionTip = false;
Vue.use(VueRouter)

new Vue({
    el: '#app',
    store: store,
    // 利用render函数来注册组件和给容器添加节点
    render: h => h(App),
    router: router
})