<template>
    <div class="todos">
        <ul class="things_list">
            <li v-for="item in todos" :key="item.id">
                <input type="checkbox" :checked="item.isdone" @click="changedone(item.id)">
                <div class="neirong"> {{ item.title }}</div>
                <span @click="deletetodo(item.id)">Delete</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'done',
    computed:{
        todos(){
            return this.$store.state.todos.filter((todo)=>{
                return todo.isdone==true
            })
        }
    },
    methods:{
        deletetodo(id){
            this.$store.dispatch('deletetodo',id)
        },
        changedone(id){
            // console.log(id);
            this.$store.dispatch('changedone',id)
        },
    },
    mounted(){
        console.log(this.todos);
    }
}
</script>

<style scoped>
input{
        position: absolute;
        left: 5px;
        top: 7px;
        width: 20px;
        height: 20px;
    }
</style>