<template>
    <div class="todos">
        <ul class="things_list">
            <li v-for="item in todos" :key="item.id">
                <!-- <div class="anniu" @click="changedone(item.id)" ref="btn"></div> -->
                <input type="checkbox" @click="changedone(item.id)" :checked="item.isdone">
                <div class="neirong">{{ item.title }}</div>
                <span @click="deletetodo(item.id)">Delete</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'all',
    computed:{
        todos(){
            return this.$store.state.todos
        }
    },
    methods:{
        changedone(id){
            // console.log(id);
            this.$store.dispatch('changedone',id)
        },
        deletetodo(id){
            this.$store.dispatch('deletetodo',id)
        }
    }
}
</script>

<style scoped>
    input{
        position: absolute;
        left: 5px;
        top: 7px;
        width: 20px;
        height: 20px;
    }
</style>