<template>
  <div class="header">
                <h1>TodoList</h1>
                <div class="in" @click="onfoucs" ref="one">
                    <input type="text" ref="two" placeholder="Please input your todo" @keyup.enter="addtodo" @blur="hide_del">
                    <span class="delete" ref="del" @click="del"></span>
                </div>
            </div>
</template>

<script>
import {nanoid} from 'nanoid'
export default {
    name:'top',
    data(){
        return {
            // cla:active
        }
    },
    methods:{
        addtodo(e){
            const obj={id:nanoid(),title:e.target.value,isdone:false}
            this.$store.dispatch('addtodo',obj)
            e.target.value=''
            // this.$refs.del.style.display='none';

        },
        onfoucs(){
            this.$refs.del.style.display='block';
        },
        hide_del(){
            this.$refs.del.style.display='none';
            // console.log('hide_del');
        },
        del(){
            this.$refs.two.value=''
        }
    }
}
</script>

<style scoped>
    .delete{
        font-family: 'icomoon';

    }
</style>